<template>
    <transition name="custom-classes-transition"
                enter-active-class="animated fadeIn login"
                leave-active-class="animated fadeOut">
        <div class="login-popup" v-show="loginShow">
            <div class="login-dialog animated fadeInDown" v-show="loginShow && !wxShow">
                <button class="login-btn-close" @click="closeLogin"></button>
                <img class="login-dialog-left" src="../assets/img/pic_login.png" alt="">
                <div class="login-dialog-right animated"
                     :class="{bounceIn: isChanging}">
                    <h4 class="login-dialog-tit">{{loginText[loginType].tit}}</h4>
                    <span class="login-dialog-span">{{loginText[loginType].span1}}</span>
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-if="inputType1"
                           type="password"
                           v-model="val1">
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-else
                           type="text"
                           v-model="val1">
                    <span class="login-dialog-span">{{loginText[loginType].span2}}</span>
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-if="inputType2"
                           type="password"
                           v-model="val2"
                           @keyup.enter="handleEnter">
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-else
                           type="text"
                           v-model="val2"
                           @keyup.enter="handleEnter">
                    <div class="login-btns-type-0"
                         v-if="loginType === 0">
                        <div class="login-btns-type-0-top">
                            <button class="login-forget" @click="jumpForget"></button>
                            <button class="login-sign-up" @click="jumpSignUp"> >></button>
                        </div>
                        <button class="login-sign-in" @click="handleSignIn"></button>
                        <span class="login-or">or</span>
                        <button class="login-sign-wx"
                                @click="weixinSign"></button>
                    </div>
                    <div class="login-btns-type-1"
                         v-if="loginType === 1 || loginType === 3">
                        <button class="login-btn-main" @click="doneVerify">{{loginText[loginType].btn}}</button>
                        <button class="login-verify-code" :class="{gray: countdown}" @click="sendVerifyCode">
                            {{countdown ? `${countdown}` : ''}}

                        </button>
                    </div>
                    <div class="login-btns-type-2"
                         v-if="loginType === 2 || loginType === 4">
                        <button class="login-btn-main" @click="setPass"></button>
                    </div>
                    <!--<span class="login-tips">{{loginTips}}</span>-->
                </div>
            </div>
            <div class="wx-dialog animated fadeInDown" v-show="wxShow">
                <button class="login-btn-close" @click="closeLogin"></button>
                <h4 class="wx-dialog-tit"><i class="login-sign-wx"></i></h4>
                <div id="wx-qrcode"></div>
                <!--<img src="../assets/img/pic-qrcode.png" alt="" class="wx-dialog-qrcode">-->
                <!--<span class="wx-dialog-span"></span>-->
            </div>
        </div>
    </transition>
</template>

<script>
    import bus from '../state/bus';
    import api from '../state/api';
    import func from '../func/func';
    let queryByName = func.getQueryStringByName;

    export default {
        name: 'login',
        data() {
            return {
                loginShow: false,
                wxShow: false,
                isChanging: false, // type
                countdown: 0, // 
                val1: '',
                val2: '',
                // 5 0-4 
                loginType: 0,
                loginText: [
                    {
                        tit: '',
                        span1: '',
                        span2: '',
                    },
                    {
                        tit: '',
                        span1: '',
                        span2: '',
                        btn: '',
                    },
                    {
                        tit: '',
                        span1: '',
                        span2: '',
                    },
                    {
                        tit: '',
                        span1: '',
                        span2: '',
                        btn: '',
                    },
                    {
                        tit: '',
                        span1: '',
                        span2: '',
                    },
                ],
                tel: '', // 
                isSign: false, // 
            }
        },
        computed: {
            inputType1() {
                let result;
                const type = this.loginType;
                if (type === 2 || type === 4) {
                    // password
                    result = 1;
                } else {
                    // text
                    result = 0;
                }
                return result;
            },
            inputType2() {
                let result;
                const type = this.loginType;
                if (type === 1 || type === 3) {
                    // text
                    result = 0;
                } else {
                    // password
                    result = 1;
                }
                return result;
            },
        },
        methods: {
            // login
            closeLogin () {
                this.loginShow = false;
                this.wxShow = false;
                this.clearVal();
            },
            //  
            clearVal () {
                this.val1 = '';
                this.val2 = '';
            },
            // 
            verifyPhone (val) {
                let regPhone = /(086-?)?(13|14|15|18)\d{9}/;
                if (!val.match(regPhone)) {
                    this.$message.warning('');
                    return 1;
                } else {
                    return 0;
                }
            },
            // 
            verifyPass (val) {
                let regPass = /([a-zA-Z0-9]|[._]){5,20}$/;
                if (!val.match(regPass)) {
                    this.$message.warning('5-20');
                    return 1;
                } else {
                    return 0;
                }
            },
            // 
            weixinSign () {
                this.wxShow = true;
            },
            // 
            handleSignIn () {
                if (!this.val1 || !this.val2) {
                    this.$message.warning('');
                    return;
                }
                // 
                if (this.verifyPhone(this.val1)) {
                    return;
                }
                if (this.verifyPass(this.val2)) {
                    return;
                }
                this.$http.post(api.sign_in, {
                    tel: this.val1,
                    password: this.val2
                })
                    .then(res => {
                        switch (parseInt(res.data.status)) {
                            case 100:
                                this.$message.warning('');
                                this.loginType = 1; // 
                                break;
                            case 200:
                                this.$message.success('');
                                this.loginShow = false;
                                this.clearVal();
                                bus.$emit('signIn'); // header
                                break;
                            default:
                                this.$message.error('');
                                break;
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            // btn
            jumpForget () {
                this.clearVal();
                this.loginType = 3;
            },
            // btn
            jumpSignUp () {
                this.clearVal();
                this.loginType = 1;
            },
            // 
            sendVerifyCode () {
                if (this.verifyPhone(this.val1) || this.countdown) {
                    return;
                }
                this.$http.post(api.get_verify_code, {tel: this.val1}).then(res => {
                    console.log(res);
                    switch (res.data.data) {
                        case 100:
                            this.$message.error('');
                            break;
                        case 200:
                            this.$message.success('');
                            this.countdown = 60;
                            let timer = setInterval(() => {
                                this.countdown--;
                                if (this.countdown === 0) {
                                    clearInterval(timer);
                                }
                            }, 1000);
                            break;
                        default:
                            break;
                    }
                }).catch(err => {
                    console.log(err);
                });
            },
            //  
            doneVerify () {
                this.$http.post(api.check_verify_code,
                    {code: this.val2}
                ).then(res => {
                    switch (parseInt(res.data)) {
                        //                        case '100':
                        //                            this.$message.error('');
                        //                            break;
                        case 200:
                            this.$message.success('');
                            this.tel = this.val1;
                            this.clearVal();
                            if (this.loginType === 1) { // 
                                this.loginType = 2;
                            } else { // 
                                this.loginType = 4;
                            }
                            break;
                        default:
                            this.$message.error('');
                            break;
                    }
                }).catch(err => {
                    console.log(err);
                });
            },
            //  
            setPass () {
                if (this.val1 && this.val1 === this.val2) {
                    this.$http.post(api.set_password, {mobile: this.tel, pwd: this.val2}).then(res => {
                        switch (parseInt(res.data.result)) {
                            case 200:
                                this.$message.success('');
                                this.loginShow = false;
                                bus.$emit('signIn'); // header
                                this.clearVal();
                                break;
//                            case 100:
//                                this.$message.warning('');
//                                break;
//                            default:
//                                break;
                        }
                    }).catch(err => {
                        console.log(err);
                    });
                } else {
                    this.$message.error(', ');
                }
            },
            // 
            handleEnter () {
                switch (this.loginType) {
                    case 0:
                        // 
                        this.handleSignIn();
                        break;
                    case 1:
                    case 3:
                        // 
                        this.doneVerify();
                        break;
                    case 2:
                    case 4:
                        // 
                        this.setPass();
                        break;
                }
            }
        },
        created() {
            //  
            if (queryByName('first')) {
                this.loginShow = true;
                this.loginType = 1;
                this.$message.success('');
            }

            // 
            // url querylogin=0
            if (queryByName('login')) {
                this.loginShow = true;
                this.loginType = 0;
                this.$message.warning('');
            }

            // this thisbusbind(this) hack;
            bus.$on('login', arg => {
                this.loginShow = arg.loginShow;
                this.loginType = arg.loginType;
            });
        },
        mounted () {
            let wx = new WxLogin({
                id: "wx-qrcode",
                appid: "wxc909bc6835c99bfb",
                scope: "snsapi_login",
                redirect_uri: "http%3A%2F%2Fcivil.gyl.sunday.so%2Findex%2Fregister%2Fweb_access",
            });
        },
        watch: {
            loginType() {
                this.isChanging = true;
                setTimeout(() => {
                    this.isChanging = false;
                }, 800);
            },
        },
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .login.animated {
        animation-duration: 0.5s;
    }

    .login-popup {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.4);
        z-index: 1000;
        .animated {
            animation-duration: 0.5s;
        }
        .login-btn-close {
            background: url(../assets/img/icons/icon_close.png) no-repeat center;
            width: 64px;
            height: 64px;
            position: absolute;
            right: -32px;
            top: -32px;
            z-index: 10;
        }
        .login-sign-wx {
            display: inline-block;
            width: 64px;
            height: 64px;
            vertical-align: middle;
            background: url(../assets/img/icons/btn_weixin.png) no-repeat center;
        }
        .login-dialog {
            width: 850px;
            height: 500px;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -425px;
            margin-top: -250px;
            // transform: translate(-50%, -50%);
            border-radius: 10px;
            background: #fff;

            .login-dialog-left {
                float: left;
                width: 480px;
                height: 100%;
            }
            .login-dialog-right {
                float: right;
                width: 370px;
                height: 100%;
                padding: 67px 40px;
                position: relative;
                animation-duration: .8s;
            }
            .login-dialog-tit {
                text-align: center;
                font-size: 30px;
                color: #333;
                margin-bottom: 60px;
                font-weight: 700;
            }
            .login-dialog-span {
                display: block;
                font-size: 16px;
                color: #999;
            }
            .login-dialog-input {
                display: block;
                font-size: 16px;
                color: #666;
                width: 100%;
                border-bottom: 1px solid #666;
                padding: 8px 0;
                margin-bottom: 5px;
            }
            .login-btns-type-0 {
                .login-btns-type-0-top {
                    margin-top: 30px;
                    overflow: hidden;
                }
                .login-forget {
                    font-size: 14px;
                    color: #333;
                    float: left;
                    margin-bottom: 30px;
                }
                .login-sign-up {
                    font-size: 14px;
                    color: #CBAC86;
                    float: right;
                }
                .login-sign-in {
                    width: 200px;
                    height: 50px;
                    background: #333;
                    color: #fff;
                    font-size: 22px;
                    text-align: center;
                }
                .login-or {
                    /*margin: 0 1px;*/
                }
                .login-sign-wx {
                    margin-top: 5px;
                }
            }
            .login-verify-code {
                background: #FE6E8C;
                width: 120px;
                height: 30px;
                color: #fff;
                border-radius: 14px;
                position: absolute;
                right: 40px;
                top: 175px;
                &.gray {
                    background: #999;
                    cursor: default;
                }
            }
            .login-btn-main {
                background: #52BE71;
                width: 100%;
                height: 50px;
                font-size: 22px;
                color: #fff;
                margin-top: 60px;
            }
            .login-tips {
                display: inline-block;
                width: 100%;
                font-size: 16px;
                color: #ff4949;
                text-align: center;
                margin-top: 10px;
            }
            .login-btns-type-2 {
                .login-btn-main {
                    background: #000;
                }
            }
        }
        .wx-dialog {
            width: 400px;
            height: 550px;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -183px;
            margin-top: -255px;
            border-radius: 10px;
            background: #fff;
            text-align: center;
            .wx-dialog-tit {
                font-size: 24px;
                color: #333;
                font-weight: 700;
                margin-top: 50px;
                .login-sign-wx {
                    margin-right: 10px;
                }
            }
            .wx-dialog-qrcode {
                display: block;
                width: 210px;
                height: 210px;
                margin: 0 auto;
            }
            .wx-dialog-span {
                display: block;
                font-size: 16px;
                color: #999;
                margin-top: 10px;
            }
        }
    }
</style>


